input[type=range].double {
    left: 0;
    top: 0px;
    position: absolute;
    width: 200px;
    pointer-events: none;
}



input[type=range].double::-webkit-slider-thumb {
    box-shadow: none;
    border: 1px solid #000000;
    height: 20px;
    width: 20px;
    border-radius: 15px;
    background: #646e56;
    -webkit-appearance: none;
    margin-top: -6px;
    cursor: pointer;
    z-index: 0;
    pointer-events: auto;
}


input[type=range].double::-webkit-slider-runnable-track {
    height: 10px;
    background-color: inherit;
    z-index: 33;
    border:0;

}

.wrapper{
    position: relative;
}